.Contacts {
  grid-area: contacts;
  background-color: white;
  display: flex;
  gap: 5px;
  flex-direction: column;
  justify-content: flex-end;
  margin-bottom: 30px;
  margin-right: 20px;
  border-bottom: 2px solid lightgray;
  min-width: 250px;
}

.Contacts > span {
  display: flex;
  align-items: center;
  gap: 5px;
}

.Contacts > span > input,
.Contacts > span > .address > input {
  font-size: 20px;
}

.Contacts > span > .address {
  display: flex;
  flex-direction: column;
}
